export default {
	name: 'weui-tabs',
	props: {
		current: {
			type: Number,
			default: 0
		},
		tabs: {
			type: Array,
			default () {
				return [];
			}
		},
		sliderLeft: {
			type: Number,
			default: 0
		},
		sliderOffset: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			marginList: ['0 0%', '0 15.5%', '0 7%'] //tabs的个数对应的margin
		}
	},
	methods: {
		onClick(e) {
			this.$emit('tabClick', e);
		}
	},
}
